<template>
    <div class="row">
        <div class="offset-1 col-9  bg-white p-5 rounded-lg shadow-lg">

          <div class="p-5">
          <p class="text-2xl">Mata Pelajaran</p>
            <hr class="style6">
          </div>
            <p  v-for="(item, index) in pelajarans" :key="index" class="pl-5 font-times text-capitalize text-xl">{{index+1}}. {{item.nama_pelajaran}}</p>
        <br>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    layout:"awal",
    data(){
        return{
            pelajarans:[]
        }
    },
    mounted() {
        let fd = new FormData()
        fd.append('data', `select * from setup_pelajaran`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            this.pelajarans = res.data
            this.$forceUpdate()
        })
    },
}
</script>